.home {
  padding-top: 50px;
  width: 100%;
  height: 90vh;
  display: flex;
  align-items: left;
  flex-direction: column;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #e6bbad; /* Soft background color */
  /* background-color: #253350; */
  background: linear-gradient(to bottom, #2c3e50, #45aaf2);
  font-family: 'Roboto', sans-serif;
}

.headerContainer {
  margin-right: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
  border: 3px solid #ccc;
}

.dataBox {
  text-align: center;
  height: 300px;
  padding: 20px;
  border: 2px solid #ccc;
  border-radius: 10px;
  margin: 10px;
  flex: 1;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
}

.dataBox h1 {
  font-size: 32px;
  font-weight: bold;
  color: #333;
  font-family: 'Nunito', sans-serif;
}

.dataBox p {
  font-size: 16px;
  color: #666;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
}

.dataBox.blue {
  background-color: #ade6d8; /* Blue color */
}

.dataBox.green {
  background-color: #add8e6; /* Green color */
}

.dataBox.orange {
  background-color: #adbce6; /* Orange color */
}

@media only screen and (max-width: 650px) {
  .home {
    justify-content: center;
    align-items: center;
  }

  .headerContainer {
    border-radius: 10px;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }

  .headerContainer h1 {
    font-size: 40px;
    color: white;
  }
  
}